:root {
  --link-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  line-height: 1;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo img {
  width: 100%;
  height: auto;
}

a {
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

a:hover, a.is-active {
  color: var(--link-color);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  position: relative;
}

li {
  margin: 0 10px;
  position: relative;
}

ul li:first-child {
  margin-left: 0;
}

ul li:last-child {
  margin-right: 0;
}

a.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
